:root {

  --main-color: rgba(203, 0, 2, 255);
}

header {
  background-color: var(--main-color);
  border-radius: '10px';
  color: white;
  display: flex;
  flex-direction: row;
  font-weight: 600;
  justify-content: space-between;
}

header img, button {
  background-color: var(--main-color);
}

header button {
  border: none;
  padding: 10px;
}

.footer {
  background-color: var(--main-color);
  width: 100%;
}

